<!--  -->
<template>
  <div class="main">
    <el-container class="main_con">
      <el-aside width="22%" class="info_l">
        <div class="info">
          <div class="cover_out flex_c">
            <img class="pic" :src="albumInfo.pic||'noimg.jpg'" @error="imgError($event)" />
            <img class="record" src="https://h5static.kuwo.cn/www/kw-www/img/album_cover_record.d7ec8ae.png">
          </div>
          <p class="info_title">专辑简介</p>
          <p class="info_text">{{albumInfo.albuminfo|cutInfo }}</p>
        </div>
      </el-aside>
      <el-main class="info_r">

        <!-- 列表 -->
        <div>
          <div>
            <span class="title">{{albumInfo.album}}</span>
            <p class="artist flex_c">
              <!-- <img class="user_img" :src="albumInfo.uPic||'noimg.jpg'" @error="imgError($event)" /> -->
              <span>{{albumInfo.artist}}</span>
            </p>
            <p class="album_info">
              <span>语种：</span>
              <span class="album_lang">{{albumInfo.lang}}</span>
              <span>发行时间：</span>
              <span class="time">{{albumInfo.releaseDate}}</span>
            </p>
            <!-- <span class="type">{{albumInfo.tag}}</span> -->
          </div>
          <div class="btns">
            <el-button class="play" round>
              <i class="iconfont icon-icon_play_"></i>
              <span>播放全部</span>
            </el-button>
          </div>
          <!-- 列表组件 -->
          <div class="list_out">
            <music-table :pageNum="pageNum" :songList="albumInfo.musicList" v-show="code==200"></music-table>
            <music-no-data v-show="code!=200 & code!=0"></music-no-data>
          </div>
          <pagination :code="code" :currentChange="getAlbumInfo" :pageSize="30" :total="albumInfo.total"></pagination>
        </div>
        <!-- 评论区 -->
        <div>
        </div>
      </el-main>
    </el-container>
  </div>
</template>

<script>
import { HttpManager } from "../../api";
import MusicTable from "../../components/MusicTable.vue";
import MusicNoData from "../../components/MusicNoData.vue";
import Pagination from '../../components/Pagination.vue';
export default {
  name: "AlbumDetail",
  data () {
    return {
      code: 0,
      albumInfo: {},
      pageNum: 0,
    };
  },

  components: { MusicTable, MusicNoData, Pagination },

  methods: {
    getAlbumInfo (val) {
      let that = this;
      this.pageNum = val;
      HttpManager.getAlbumInfo({ albumId: this.$route.params.aid, pn: val, rn: 30 })
        .then((res) => {
          that.code = res.code;
          that.albumInfo = res.data;
          if (!('albuminfo' in that.albumInfo) || that.albumInfo.albuminfo === '') {
            that.$set(that.albumInfo, 'albuminfo', '暂无简介');
          }
        })
    },
    imgError (event) {
      event.currentTarget.src = "https://h5static.kuwo.cn/upload/image/4f768883f75b17a426c95b93692d98bec7d3ee9240f77f5ea68fc63870fdb050.png";
      // event.currentTarget.onerror = null;
    }
  },

  computed: {},
  filters: {
    cutInfo (val) {
      val = val + '';
      if (val.length > 120)
        return val.substr(0, 120) + '...';
      else
        return val;
    }
  },
  //生命周期 - 创建完成（可以访问当前this实例）
  created () {
    this.getAlbumInfo(1);
  },

  //生命周期 - 挂载完成（可以访问DOM元素）
  mounted () { },
}

</script>
<style lang="scss" scoped>
@import "../../assets/css/font";
*,
:after,
:before {
  -webkit-tap-highlight-color: transparent;
  box-sizing: border-box;
}
.main {
  margin-top: 20px;
  padding: 0 120px;
  display: block;
}
.main_con {
  min-width: 750px;
  height: 100%;
  font-size: 14px;
  line-height: 1;
  font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
    sans-serif;
  .info_l {
    min-height: 300px;
    min-width: 135px;
    max-width: 180px;
    background: #fff;
    margin-right: 1%;
    width: 22%;
    // box-shadow: 0 0 30px 0 rgb(65 67 70 / 10%);
    .info {
      overflow: hidden;
      margin: 5px;
      box-shadow: 0 0 20px 0 rgb(65 67 70 / 10%);
      padding: 5px;
      .cover_out {
        max-height: 540px;
        overflow: auto;
        position: relative;
        width: 100%;
        .pic {
          width: 85%;
          height: 85%;
        }
        .record {
          width: 15%;
        }
      }
      .info_title {
        margin: 20px 0 10px;
        font-size: 20px;
        font-weight: 600;
        height: 30px;
        line-height: 30px;
      }
      .info_text {
        width: 100%;
        font-size: 13px;
        color: #888;
        line-height: 20px;
        margin-bottom: 20px;
      }
    }
  }
  .info_r {
    position: relative;
    width: 76.14%;
    z-index: 10;
    margin-left: 10px;
    padding: 0;
    .title {
      vertical-align: baseline;
      display: inline-block;
      margin: 20px 0 0;
      height: 30px;
      line-height: 30px;
      font-size: 28px;
      font-weight: 600;
    }
    .artist {
      font-size: 18px;
      height: 25px;
      color: #666;
      line-height: 25px;
      font-weight: 400;
      margin-top: 15px;
    }
    .album_info {
      color: #999;
      margin-top: 15px;
      font-size: 14px;
      font-family: "Microsoft YaHei", "Helvetica Neue", Helvetica, STHeiTi,
        sans-serif;
      .album_lang,
      .time {
        margin-right: 30px;
        color: #000;
      }
    }
    .btns {
      margin: 25px 0 20px;
      .play {
        width: 130px;
        background: #31c27c;
        color: #fff;
        border: none;
        &:hover {
          color: #000;
        }
        &:active {
          box-shadow: 0 0 10px 0 rgb(65 67 70 / 30%) inset;
          border: none;
        }
      }
    }
    .list_out {
      box-shadow: 0 0 20px 0 rgb(65 67 70 / 8%);
    }
  }
}
.flex_c {
  display: flex;
  align-items: center;
}
</style>




